* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: whitesmoke;
}

header {
  height: 100px;
  display: flex;
  align-items: center;
  background: #6441A5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2a0845, #6441A5);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2a0845, #6441A5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

header > img {
  height: 80%;
  margin-left: 15%;
}

header > p {
  font-family: VT323, monospace;
  color: whitesmoke;
  font-size: 50px;
  margin-left: 0.8em;
}

.meme {
  position: relative;
  width: 50%;
  margin: auto;
}

.meme > img {
  width: 100%;
}

.meme > h2 {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.meme > .bottom {
  bottom: 0;
}

.meme > .top {
  top: 0;
}

.meme-form {
  width: 90%;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
}

.meme-form > input {
  width: 45%;
  height: 40px;
}
::placeholder{
  color: #6441A5;
}
.meme-form > button {
  border: none;
  font-family: VT323, monospace;
  font-size: 25px;
  letter-spacing: 1.5px;
  color: white;
  background: #6441A5;
}

.meme-form > input::-webkit-input-placeholder { /* Chrome/Opera/Safari */
font-family: VT323, monospace;
font-size: 25px;
text-align: cen
}
.meme-form > input::-moz-placeholder { /* Firefox 19+ */
font-family: VT323, monospace;
font-size: 25px;
text-align: cen
}
.meme-form > input:-ms-input-placeholder { /* IE 10+ */
font-family: VT323, monospace;
font-size: 25px;
text-align: cen
}
.meme-form > input:-moz-placeholder { /* Firefox 18- */
font-family: VT323, monospace;
font-size: 25px;
text-align: cen
}

/*for larger screens*/
@media only screen and (min-width: 2560px){
  header{
    height: 160px;
  }
  header > img {
    margin-left: 18%
  }
  header > p {
    margin-left: 0.2em;
    font-size: 100px;
    font-weight: normal;
  }
  .meme-form{
    width: 50%
  }
  .meme-form > input{
    height: 100px;
    font-size: 200%;
  }
  .meme > h2 {
    font-size: 5em;
  }
  
}
/*regular laptop screens*/
@media only screen and (max-width: 1440px){
  header{
    height: 120px;
  }
  header > p {
    margin-left: 0.5em;
    font-weight: bold;
  }
  .meme-form > input{
    height: 60px;
    font-size: 120%;
  }
  .meme > h2 {
    font-size: 3em;
  }
  
}

/*for smaller screens*/
@media only screen and (max-width: 1024px){ /*small laptop*/
  header{
    height: 80px;
  }
  header > img {
    margin-left: 10%;
  }
  .meme-form{
    width: 80%;
  }
  input {
    height: 45px;
    font-size: 100%;
  }
  .meme{
    width: 90%
  }
  .meme h2{
    font-size: 3em;
  }

}

@media only screen and (max-width: 768px){ /*tablet*/
  .meme {
    width: 80%
  }
  .meme > h2 {
    margin-top: 0.1em;
    margin-bottom: 0.1em;
    width: 100%;
  }
  .meme-form{
    width: 100%;
  }
 
  header > p{
    margin-left: 0.4em;
  }
}

@media only screen and (max-width: 425px){/*phones*/
  .meme {
    width: 100%;
  }
  .meme > h2  {
    margin-left: 0.3em;
    font-size: 2em;
  }
  header{
    height: 60px;
  }
  header > p {
    margin-left: 5%;
    line-height: 0.85em;
    text-align: left;
    font-size: 28px;
  }
  header > img {
    margin-left: 1em;
  }
  .meme-form{
    display: grid;
    grid-template-columns: 70% 30%;
  }
  .meme-form > input{
    grid-column: 1;
    height: 40px;
    width: 100%
  }
  .meme-form > button{
    grid-row: 1 / span 2;
    grid-column: 2;
    
  }
}

@media only screen and (max-width: 320px){
  .meme > h2  {
    margin-left: 0.1em;
    font-size: 1.5em;
  }
   
}


